@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: empire;
  src: url("./assets/fonts/empire.otf");
}

@font-face {
  font-family: mont;
  src: url("./assets/fonts/mont.ttf");
}

@font-face {
  font-family: montbold;
  src: url("./assets/fonts/mont-bold.ttf");
}

@font-face {
  font-family: ubuntu;
  src: url("./assets/fonts/ubuntu.ttf");
}

@font-face {
  font-family: ubuntubold;
  src: url("./assets/fonts/ubuntu-bold.ttf");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: 0.3s;
  scroll-behavior: smooth;
}

.fontempire {
  font-family: empire;
  letter-spacing: 3px;
}

.mont {
  font-family: mont;
  letter-spacing: 1px;
}

.montbold {
  font-family: montbold;
  letter-spacing: 1px;
}

.ubuntu {
  font-family: ubuntu;
  letter-spacing: 1px;
}

.ubuntubold {
  font-family: ubuntubold;
  letter-spacing: 1px;
}

#home {
  background: url("./assets/img/home.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

#about {
  background: url("./assets/img/about.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

#product {
  background: url("./assets/img/products.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#care {
  background: url("./assets/img/wecare.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

#award {
  background: url("./assets/img/award.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

#contact {
  background: url("./assets/img/contact.png");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

p {
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  font-family: ubuntu;
  border-radius: 1pc;
  height: auto;
  text-align: justify;
  border: 2px solid black;
}

p:hover {
  background-color: white;
}

p span {
  font-family: montbold;
}

.bg-blurw {
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  border-radius: 1pc;
  border: 2px solid black;
}

.hover-w:hover {
  background-color: white;
}

.bg-blurb {
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  border-radius: 1pc;
}

::-webkit-scrollbar {
  width: 8px;
  background-color: black;
}

::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 1pc;
}
